import React from 'react';
import AddFishForm from './AddFishForm'

class Inventory extends React.Component{
    constructor(){
        super();
        this.renderInventory = this.renderInventory.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }


 handleChange(e,key){
     const fish = this.props.fishes[key];
     const updatedFish = {
         ...fish, [e.target.name] : e.target.value
     }
     this.props.updateFish(key, updatedFish)

 }


    renderInventory(key){
        const fish = this.props.fishes[key]
        return (
            < div className="fish-edit-form" key ={key}>
                    <input value={fish.name} type="text" placeholder="Fish Name " className='fish-name' onChange = { (e) => this.handleChange(e, key)} />
                    <input value={fish.price}  type="text" placeholder="Fish Price " className='fish-price' onChange = { (e) => this.handleChange(e, key)}/>
                    <select value={fish.selection} className="fish-selection" onChange = { (e) => this.handleChange(e, key)}>
                        <option value="Available"> Fresh! </option>
                        <option value="Unavilable"> Sold Out !!!</option>
                    </select>
                    <textarea value={fish.descp}  type="text" placeholder="Fish Description" className="fish-desc" onChange = { (e) => this.handleChange(e, key)}/>
                    <input value={fish.image}  type="text" placeholder="Fish  Image" className="fish-image"onChange = { (e) => this.handleChange(e, key)} />
                    <button className="submit-button" type="submit"> + Add Item</button>
                    <button className="submit-button" onClick={()=> this.props.removeFish(key)}> - Remove Fish</button>
                </div>
        )
    }
    render(){
        return (
            <>
            <div className="main-inventory-app">
                <h2> Inventory</h2>
                {Object
                .keys(this.props.fishes)
                .map(this.renderInventory)}
                <AddFishForm addFish={this.props.addFish}/>
                <button className="submit-samples" onClick={this.props.loadSample}>See Fishes </button>
            </div>
            </>
        )
    }
}

export default Inventory;