import React from 'react';
import Inventory from './Inventory';
import Header from './Header';
import Order from './Order';
import sampleFish from './sampleFish';
import Fish from './Fish';
// import base from '../base';

class App extends React.Component {
    constructor() {
        super();
        this.addFish = this.addFish.bind(this);
        this.removeFromOrder = this.removeFromOrder.bind(this);
        this.updateFish = this.updateFish.bind(this);
        this.removeFish = this.removeFish.bind(this);
        this.loadSample = this.loadSample.bind(this);
        this.addToOrder = this.addToOrder.bind(this);
        this.state = {
            fishes: {},
            order: {}
        };
    }

    componentWillMount() {
        // this.ref = base.syncState(`${this.props.params.App}/fishes`,{constext: this,state: "fishes"});
        const localStorageRef = localStorage.getItem(`Order`);
        if (localStorageRef) {
            this.setState({
                order: JSON.parse(localStorageRef)
            });
        }
    }

    // componentWillUnmount() {
    //     base.removeBinding(this.ref)
    // }

    componentWillUpdate(nextProps, nextState) {
        localStorage.setItem(`Order`, JSON.stringify(nextState.order))

    }


    addFish(fish) {
        const fishes = { ...this.state.fishes }
        const timeStamp = Date.now();
        fishes[`fish-${timeStamp}`] = fish;
        this.setState({ fishes });
    }

    updateFish(key,updatedFish){
        const fishes = {...this.state.fishes}
        fishes[key] = updatedFish;
        this.setState({fishes});

    }
    removeFish(key){
        const fishes = {...this.state.fishes}
        delete fishes[key]
        //fishes[key] = null;
        this.setState({fishes});


    }

    loadSample() {
        this.setState({ fishes: sampleFish })
    }

    addToOrder(key) {
        const order = {...this.state.order };
        order[key] = order[key] + 1 || 1;
        this.setState({ order })

    }
    
    removeFromOrder(key){
        const order = {...this.state.order};
        delete order[key]
        this.setState({order});
    }

    render() {
        return (
            <>
                <div className="main-body-app">
                    <div className="fish-menu-main">

                        <div className="main-menu">
                            <Header tagLine="Delicious food that Swims." />

                        </div>
                        <ul className="list-of-fishes"></ul>
                        {
                            Object
                                .keys(this.state.fishes)
                                .map(key => <Fish key={key} index={key} details={this.state.fishes[key]} addToOrder={this.addToOrder} />)
                        }
                    </div>

                    <Order
                        params={this.props.params}
                        fishes={this.state.fishes}
                        order={this.state.order}
                        removeFromOrder = {this.removeFromOrder}

                    />

                    <Inventory
                        fishes={this.state.fishes}
                        addFish={this.addFish}
                        loadSample={this.loadSample}
                        removeFish = {this.removeFish}
                        updateFish= {this.updateFish}
                        

                    />
                </div>
            </>
        )
    }
}

export default App;