import React from 'react';

class Order extends React.Component {
    constructor(){
        super();
        this.renderOrder = this.renderOrder.bind(this);
    }
    renderOrder(key) {
        const fish = this.props.fishes[key];
        const count = this.props.order[key];
        const removeButton = <button className="removebutton" onClick={() => this.props.removeFromOrder(key) } > x </button>
        if (!fish || fish.selection === "unavilable"){
            return( <>
                <li> key = {key} sorry, {fish ? fish.name : "fish"} is not available {removeButton}</li>
            </>)
        }
        return(
            <>
                <li key = {key}>
                <span className="order-fish-name" onClick={()=> this.props.removeFromOrder(key)}>
                    {fish.name} {removeButton}
                </span> 
                <span className="order-price-fish">  $ {count * fish.price} </span>
                </li>
            </>
        )
    }

    render() {
        const orderIds = Object.keys(this.props.order);
        const total = orderIds.reduce = ((prevTotal, key) => {
            const fish = this.props.fishes[key];
            const count = this.props.order[key];
            const isAvailable = fish && fish.selection === "available";
            if(isAvailable) {
                return prevTotal + (count* fish.price || 0)
            } 
        }, 0);
        return (
            <>
                <div className="main-order-app">
                    <h2>Your Order</h2>
                    {orderIds.map(this.renderOrder)}
                    <li className="total-price-order"> <span className="order-total">Total : </span>   <span className="total-amount">$ {total}</span></li>
                </div>
            </>
        )
    }
}

export default Order;