import React from 'react';

class Fish extends React.Component {
    render() {
        const { details, index } = this.props;
        const isAvailable = details.selection === "available";
        const buttonText = isAvailable ? 'Add To Order' : "Sold Out";
        return (
            <div>
                <li className="fishmenu-list">
                    <div className="fish-main-body">
                        <div className="fish-main-image">
                            <div className="image-fish"><img src={details.image} alt={details.name} /></div>
                        </div>

                        <div className="fish-content">
                            <h2 className="fish-name-conatiner">{details.name}
                                <span className="price">
                                    {details.price}
                                </span>
                            </h2>
                            <p> {details.descp}</p>
                            <button onClick={() => this.props.addToOrder(index)} disabled={!isAvailable} className="add-order">{buttonText}</button></div>
                    </div>

                </li>
            </div>
        )
    }
}


export default Fish;