import React from 'react';

class AddFishForm extends React.Component {

    createFish(event) {
        event.preventDefault();
        console.log('GOnna Make Some fish')
        const fish = { 
            name: this.name.value,
            price: this.price.value,
            selection: this.selection.value,
            descp: this.descp.value,
            image: this.image.value,
        };
    this.props.addFish(fish);
    this.fishForm.reset();
    };

    render() {
        return (
            <>
                <form ref={(input) => this.fishForm = input} className="fish-edit-form" onSubmit={(e) => this.createFish(e)}>
                    <input ref={(input)=> this.name = input} type="text" placeholder="Fish Name " className='fish-name' />
                    <input ref={(input)=> this.price = input} type="text" placeholder="Fish Price " className='fish-price' />
                    <select ref={(input)=> this.selection = input} className="fish-selection">
                        <option value="Available"> Fresh! </option>
                        <option value="Unavilable"> Sold Out !!!</option>
                    </select>
                    <textarea ref={(input)=> this.descp = input} type="text" placeholder="Fish Description" className="fish-desc" />
                    <input ref={(input)=> this.image = input} type="text" placeholder="Fish  Image" className="fish-image" />
                    <button className="submit-button" type="submit"> + Add Item</button>
                </form>
            </>
        )
    }
}


export default AddFishForm;